<template>
  <!-- <n-space vertical size="large" style="height: 100vh; width: 100%">
    <n-layout
      v-if="bookInfo !== undefined"
      position="absolute"
      style="margin-top: 20px"
    > -->
  <n-space vertical size="large" style="height: 100vh; width: 100%">
    <n-layout
      position="absolute"
      style="width: 100%; margin-top: 20px"
      v-if="bookInfo !== undefined"
    >
      <n-layout-content id="display_area" content-style="padding-top: 10px;">
        <!-- <n-layout-content content-style="padding-top: 40px;" id="display_area"> -->
        <n-space vertical style="width: 100%">
          <n-space justify="center" align="center" style="width: 100%">
            <n-space
              id="info_box"
              ref="info_box"
              style="width: 100%"
              justify="center"
              align="start"
            >
              <div
                inline
                id="image_box"
                ref="image"
                v-if="bookInfo !== undefined"
              >
                <n-image
                  id="cover_image"
                  :width="image_width"
                  :height="0"
                  :src="`/api/cover/${bookInfo.cover_name}`"
                />
              </div>
              <div id="info_gap" />
              <div id="detail_box" :style="detailBoxStyle">
                <text id="title">{{ bookInfo.title }}</text>
                <n-divider />
                <n-space>
                  <text id="author" class="author">作者:</text>
                  <n-a
                    @click="jumpToAuthorSearch(author.name)"
                    class="author"
                    v-for="author in bookInfo.authors"
                    :key="author.name"
                    >{{ author.name }}</n-a
                  >
                </n-space>
                <n-space>
                  <text
                    id="series"
                    class="series"
                    v-if="bookInfo.series.length != 0"
                    >丛书:</text
                  >
                  <n-a
                    class="series"
                    v-for="series in bookInfo.series"
                    :key="series.name"
                    @click="jumpToSeriesSearch(series.name)"
                    >{{ series.name }}</n-a
                  >
                </n-space>
                <n-divider />
                <n-space>
                  <n-tag
                    type="success"
                    size="small"
                    round
                    v-for="tag in bookInfo.tags"
                    @click="jumpToTagSearch(tag)"
                    :key="tag"
                  >
                    {{ tag }}
                  </n-tag>
                </n-space>
                <n-divider />
                <n-space>
                  <text class="rating">评分:</text>
                  <n-rate
                    readonly
                    allow-half
                    :value="bookInfo.rating ? bookInfo.rating : 0"
                  />
                </n-space>
                <n-divider />
                <n-space>
                  <n-ellipsis
                    expand-trigger="click"
                    line-clamp="7"
                    :tooltip="false"
                  >
                    {{ bookInfo.description }}
                  </n-ellipsis>
                </n-space>
              </div>
            </n-space>
          </n-space>
          <n-tabs type="line" justify-content="space-evenly">
            <n-tab-pane name="the beatles" tab="章节">
              <n-collapse>
                <n-collapse-item
                  :title="part.name"
                  v-for="(part, index) in bookInfo.content_info"
                  :key="index"
                >
                  <div>
                    <n-button
                      quaternary
                      round
                      type="primary"
                      v-for="(chapters, index) in part.chapters"
                      :key="index"
                      @click="chapterRead(chapters.href)"
                      >{{ chapters.name }}</n-button
                    >
                  </div>
                  <!-- <div style="height: 100px"/> -->
                </n-collapse-item>
              </n-collapse>
            </n-tab-pane>
            <n-tab-pane name="oasis" tab="信息"> </n-tab-pane>
          </n-tabs>
          <div style="height: 100px" />
        </n-space>
      </n-layout-content>
    </n-layout>
    <n-space align="end" style="position: absolute; width: 97.5%" justify="end">
      <n-button
        text
        style="font-size: 24px; margin-top: 10px"
        @click="downloadBook"
      >
        <n-icon>
          <CloudDownloadOutline />
        </n-icon>
      </n-button>
      <n-button
        text
        style="font-size: 24px; margin-top: 10px"
        @click="showModal = true"
      >
        <n-icon>
          <pencil-sharp />
        </n-icon>
      </n-button>
      <n-modal
        v-if="bookInfo !== undefined"
        v-model:show="showModal"
        id="edit_box"
      >
        <book-info-editer :bookInfo="bookInfo" />
      </n-modal>
    </n-space>
    <model-progress :percentage="downloadRate" :show="showDownload" />
  </n-space>

  <!-- <n-space id="spinner" v-if="bookInfo === undefined">
    <n-spin size="large" />
  </n-space>ÏƒÏ -->
</template>

<style lang="scss">
#spinner {
  position: absolute;
  top: 0px;
  bottom: 0px;
  left: 0px;
  right: 0px;
}
#info_box {
  width: 100%;
}
#detail_box {
  background-color: green($color: #f70000);
  width: 500px;
}
#title {
  font-weight: bold;
  color: #6d7070;
}
.author {
  font-weight: bold;
  color: #878b8b;
}
.series,
.rating {
  font-weight: bold;
  color: #949999;
}
.chapters {
  margin-left: 20px;
  margin-trim: 10px;
  font-size: 18px;
}
</style>
<script>
import $ from "jquery";
import { PencilSharp, CloudDownloadOutline } from "@vicons/ionicons5";
import BookInfoEditer from "@/components/BookInfoEditer.vue";
import { useLoadingBar } from "naive-ui";
import ModelProgress from "@/components/ModelProgress.vue";
import axios from "axios";
export default {
  setup() {
    const loadingBar = useLoadingBar();
    return {
      handleStart() {
        loadingBar.start();
      },
      handleFinish() {
        loadingBar.finish();
      },
      handleError() {
        loadingBar.error();
      },
    };
  },
  async created() {
    this.handleStart();
    this.getBookInfo();
  },
  data() {
    return {
      image_width: 0,
      width: 0,
      info_box_width: 0,
      detail_box_width: 0,
      showModal: false,
      bookInfo: undefined,
      showDownload: false,
      downloadRate: 0,
    };
  },
  props: {
    book_id: {
      type: Number,
      required: true,
    },
  },
  mounted() {
    this.$nextTick(() => {
      window.addEventListener("resize", this.calculateWidth);
      this.emitter.on("menu_collapsed", (e) => {
        setTimeout(this.calculateWidth, 300);
      });
    });
  },
  methods: {
    calculateWidth() {
      this.info_box_width = $("#display_area")[0].clientWidth;
      // console.log('Changed!!' + this.info_box_width)
    },
    elementsSizeSet() {
      $("#title").css("font-size", this.info_box_width * 0.03);
      $("#info_gap").css("width", this.info_box_width * 0.005);
      $(".author").css("font-size", this.info_box_width * 0.015);
      $(".series").css("font-size", this.info_box_width * 0.012);
    },
    async getBookInfo() {
      fetch("/api/info", {
        method: "post",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ book_id: this.book_id, token: "123456" }),
      })
        .then((response) => response.json())
        .then((data) => (this.bookInfo = data))
        .then(this.calculateWidth)
        .then(this.calculateWidth);
    },
    chapterRead(chapterHref) {
      console.log(this.bookInfo.content_info);
      this.$router.push({
        name: "book.read",
        params: {
          book_id: this.book_id,
          slected_page: chapterHref,
        },
      });
    },
    jumpToTagSearch(tag_name) {
      this.$router.push({
        name: "book.list",
        params: {
          query: "tag",
          fuzzy: 0,
          matcher: tag_name,
        },
      });
    },
    jumpToAuthorSearch(author_name) {
      this.$router.push({
        name: "book.list",
        params: {
          query: "author",
          fuzzy: 0,
          matcher: author_name,
        },
      });
    },
    jumpToSeriesSearch(series_name) {
      this.$router.push({
        name: "book.list",
        params: {
          query: "series",
          fuzzy: 0,
          matcher: series_name,
        },
      });
    },
    downloadBook() {
      this.showDownload = true;
      this.downloadRate = 0;
      let postJson = {
        token: "2324",
        book_id: this.bookInfo.book_id,
        is_download: true,
        href: 'null'
      };
      var config = {
        onUploadProgress: (progressEvent) => {
          var complete =
            ((progressEvent.loaded / progressEvent.total) * 100) | 0;
          this.downloadRate = complete;
        },
        responseType: "blob",
      };
      axios
        .post(`/api/content`, postJson, config)
        .then((response) => {
          const url = window.URL.createObjectURL(new Blob([response.data]));
          const link = document.createElement("a");
          link.href = url;
          let extention = ''
          if(this.bookInfo.file_type.search("epub") > 0) {
            extention = "epub"
          } else if(this.bookInfo.file_type.search("txt") > 0) {
            extention = "txt"
          } else {
            extention = 'zip'
          }
          link.setAttribute("download", `${this.bookInfo.title}.${extention}`);
          document.body.appendChild(link);
          link.click();
          this.showDownload = false
          this.downloadRate = 0
        });
    },
  },
  computed: {
    detailBoxStyle() {
      return {
        width: this.detail_box_width + "px",
      };
    },
  },
  watch: {
    info_box_width: function () {
      this.image_width = this.info_box_width * 0.35;
      this.detail_box_width = this.info_box_width * 0.57;
      this.elementsSizeSet();
    },
    bookInfo() {
      this.handleFinish();
    },
    showModal(newValue, oldValue) {
      if (!newValue && oldValue) {
        this.$router.go();
      }
    },
  },
  components: {
    PencilSharp,
    BookInfoEditer,
    ModelProgress,
    CloudDownloadOutline,
  },
};
</script>