<template>
  <n-card
    style="width: 700px"
    title="元数据编辑"
    :bordered="false"
    size="huge"
    role="dialog"
    aria-modal="true"
  >
    <n-space vertical>
      <n-card title="基本信息" size="small">
        <n-space align="end">
          <text class="series">书名:</text>
          <n-input
            id="title_input"
            v-model:value="titleValue"
            type="text"
            :placeholder="bookInfo.title"
          />
          <text class="series">作者:</text>
          <n-input
            id="author_input"
            v-model:value="authorValue"
            type="text"
            :placeholder="authorName"
          />
        </n-space>
        <n-space align="end" style="margin-top: 5px">
          <text class="series">系列:</text>
          <n-input
            id="series_input"
            v-model:value="seriesValue"
            type="text"
            :placeholder="seriesName"
          />
          <text class="series">编号:</text>
          <n-input-number
            clearable
            id="series_number_input"
            v-model:value="seriesId"
          />
        </n-space>
        <n-space align="end">
          <text class="series">出版:</text>
          <n-input
            id="publisher_input"
            v-model:value="publisherValue"
            type="text"
            :placeholder="publisherName"
          />
          <text class="series">日期:</text>
          <n-date-picker
            id="published_time_picker"
            type="date"
            :default-value="defaultPublishedTime"
            v-model:value="publishedTimestamp"
            clearable
          />
        </n-space>
      </n-card>
      <n-card title="其他信息" size="small">
        <text class="series">简介:</text>
        <n-input
          v-model:value="descriptionValue"
          type="textarea"
          :autosize="{ minRows: 4, maxRows: 10 }"
          style="margin-bottom: 5px"
          :placeholder="bookInfo.description"
        />
        <n-space align="end" style="margin-top: 10px; margin-bottom: 10px">
          <text class="series">评分:</text>
          <n-rate
            allow-half
            :default-value="bookInfo.rating"
            v-model:value="ratingValue"
          />
          <text class="series" style="margin-left: 90px">适配器:</text>
          <n-select
            v-model:value="fileTypeValue"
            filterable
            placeholder="默认TXT"
            :options="adapterOptions"
            clearable
            remote
            style="width: 240px"
          />
        </n-space>
        <text class="series">封面:</text>
        <n-space>
          <n-upload
            action="/api/update"
            style="margin-right: 175px"
            list-type="image-card"
            :data="fileData"
            :max=1
            @preview="handlePreview"
          />
          <n-modal
            preset="card"
            style="width: 600px"
            title="一张很酷的图片"
          >
            <img :src="previewImageUrl" style="width: 100%" />
          </n-modal>
          <n-space vertical>
            <n-space>
              <text class="series">书号:</text>
              <n-input
                id="identifiers_input"
                v-model:value="identifierValue"
                type="text"
                :placeholder="bookInfo.identifiers"
              />
            </n-space>
            <n-space>
              <text class="series">来源:</text>
              <n-input
                id="source_input"
                v-model:value="sourceValue"
                type="text"
                :placeholder="bookInfo.source"
              />
            </n-space>
          </n-space>
        </n-space>
      </n-card>
    </n-space>
    <template #footer>
      <n-space justify="space-between">
        <n-button type="error" @click="deleteBook">删除</n-button>
        <n-button type="success" @click="submitForm">提交</n-button>
      </n-space>
    </template>
  </n-card>
</template>
<style lang="scss">
#title_input,
#author_input,
#series_input,
#publisher_input,
#identifiers_input,
#published_time_picker,
#source_input {
  width: 230px;
}

.series {
  font-weight: bold;
  color: #949999;
}
</style>

<script>
import { defineComponent } from "vue";
import { useLoadingBar, useMessage } from "naive-ui";

export default defineComponent({
  props: {
    bookInfo: {
      type: Object,
      required: true,
    },
  },
  setup() {
    const loadingBar = useLoadingBar();
    const message = useMessage();
    return {
      createUpdateSuccessMessage() {
        message.info(
          "书籍信息更新成功哦～",
          { duration: 10000 }
        );
      },
      createUpdateErrorMessage() {
        message.error(
          "书籍信息更新失败...",
          { duration: 10000 }
        );
      },
      createDeleteSuccessMessage() {
        message.info(
          "书籍删除成功哦～",
          { duration: 10000 }
        );
      },
      handleStart() {
        loadingBar.start();
      },
      handleFinish() {
        loadingBar.finish();
      },
      handleError() {
        loadingBar.error();
      },
    };
  },
  data() {
    return {
      titleValue: "",
      authorName: "",
      authorValue: "",
      seriesName: "",
      seriesValue: "",
      publisherName: "",
      seriesId: 0,
      ratingValue: this.bookInfo.rating,
      publisherValue: "",
      publishedTimestamp: +new Date(this.bookInfo.published),
      descriptionValue: "",
      fileTypeValue: "",
      identifierValue: "",
      sourceValue: "",
      defaultPublishedTime: +new Date(this.bookInfo.published),
      fileData: { book_id: this.bookInfo.book_id },
      adapterOptions: []
    };
  },
  created() {
    this.getAdapterOptions()
    this.authorName = this.bookInfo.authors
      .map((author) => author.name)
      .join(",");
    this.seriesName = this.bookInfo.series
      .map((series) => series.name)
      .join(",");

    this.publisherName = this.bookInfo.publishers.join(",");
  },
  methods: {
    async getAdapterOptions() {
      fetch("/api/adapters", {
        method: "post",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          token: "123456",
        }),
      })
        .then((response) => response.json())
        .then((data) => this.adapterOptions = data)
        .then(console.log(this.adapterOptions))
    },
    deleteBook() {
      fetch("/api/delete", {
        method: "post",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          book_id: this.bookInfo.book_id,
          token: '12ssda'
        }),
      })
      .then(this.createDeleteSuccessMessage())
      .then(() => {this.$router.push({name: "book.list",
      params:{
        query: 'time',
        fuzzy: 0
      }
      })})
    },
    submitForm() {
      let form= {
        book_id: parseInt(this.bookInfo.book_id),
        title: this.titleValue,
        published: (new Date(this.publishedTimestamp)).toDateString(),
        identifiers: this.identifierValue,
        publisher_name: this.publisherValue.split(','),
        description: this.descriptionValue,
        rating: this.ratingValue,
        source: this.sourceValue,
        file_type: this.fileTypeValue,
        author_name: this.authorValue.split(','),
        series_name: this.seriesValue.split(','),
        token: '1asas'
      }
      this.handleStart()
      // console.log(form)
      this.updateBookInfo(form)
    },
    async updateBookInfo(form) {
      fetch("/api/update", {
        method: "post",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(form),
      })
      .then((response) => response.json())
      .then((data) => {
        if(data.msg == 'ok') {
          this.handleFinish()
          this.createUpdateSuccessMessage()
        } else {
          this.handleError()
          this.createUpdateErrorMessage()
        }
      })
    }
  },
});
</script>
